import React from 'react';
import './App.css';
import Layout from './Layout/Layout'
import {BrowserRouter,Route} from 'react-router-dom'
import Mtable from './Components/Material Table/material-table';
import Signin from './Components/Sign-In Form/Sign-in'
import Signup from './Components/Sign-Up Form/Sign-up'
import Home from './Components/Home/Home'

class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
      <Layout>
      <Route path="/signin" component={Signin} />
      <Route path="/signup" component={Signup} />
      <Route path="/material-table" component={Mtable} />
      <Route path="/" exact component={Home} />
      </Layout>
      </BrowserRouter>
    );
  }
}

export default App;
