import React from 'react'
import {Link} from 'react-router-dom'


export default class Header extends React.Component {

    state = {
        css: 'collapse navbar-collapse',
        flag: true
    }

    buttonHandler = () => {
        this.state.flag ? this.setState({ flag: false, css: 'collapse navbar-collapse' })
            : this.setState({ flag: true, css: 'collapse navbar-collapse show' })
    }
    render() {
        return (
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark">
                
                <h1 className="navbar-brand my-auto"><Link to="/" className="text-white text-decoration-none">Campus Recruitment System</Link></h1>
                <button className="navbar-toggler" type="submit" onClick={this.buttonHandler}>
                    <span className="navbar-toggler-icon">
                    </span>
                </button>
                <div className={this.state.css}>
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item mr-3">
                            <Link to="/signup" className="text-white text-decoration-none btn">Sign-Up</Link>
                        </li>
                        <li className="nav-item mr-2">
                            <Link to="/signin" className="text-white text-decoration-none btn">Sign-In</Link>
                        </li>
                        <li className="nav-item mr-2">
                            <Link to="/material-table" className="text-white text-decoration-none btn">Material Table</Link>
                        </li>
                        {/* <li className="nav-item mr-2">
                            <Link to="/table" className="text-white text-decoration-none btn">Table</Link>
                        </li> */}
                    </ul>
                </div>
            </nav>
        )
    }
}
